export default {
    after: (field, [target]) => `O campo ${field} deve estar depois do campo ${target}.`,
    alpha_dash: (field) => `O campo ${field} deve conter letras, nmeros e traos.`,
    alpha_num: (field) => `O campo ${field} deve conter somente letras e nmeros.`,
    alpha: (field) => `O campo ${field} deve conter somente letras.`,
    before: (field, [target]) => `O campo ${field} deve estar antes do campo ${target}.`,
    between: (field, [min, max]) => `O campo ${field} deve estar entre ${min} e ${max}.`,
    confirmed: (field, [confirmedField]) => `O campo ${field} e ${confirmedField} devem ser iguais.`,
    date_between: (field, [min, max]) => `O campo ${field} deve estar entre ${min} e ${max}.`,
    date_format: (field, [format]) => `O campo ${field} deve estar no formato ${format}.`,
    decimal: (field, [decimals] = ['*']) => `O campo ${field} deve ser numrico e deve conter ${decimals === '*' ? '' : decimals} casas decimais.`,
    digits: (field, [length]) => `O campo ${field} deve ser numrico e ter ${length} dgitos.`,
    dimensions: (field, [width, height]) => `O campo ${field} deve ter ${width} pixels de largura por ${height} pixels de altura.`,
    email: (field) => `O campo ${field} deve ser um email vlido.`,
    ext: (field) => `O campo ${field} deve ser um arquivo vlido.`,
    image: (field) => `O campo ${field} deve ser uma imagem.`,
    in: (field) => `O campo ${field} deve ter um valor vlido.`,
    ip: (field) => `O campo ${field} deve ser um endereo IP vlido.`,
    max: (field, [length]) => `O campo ${field} no deve ter mais que ${length} caracteres.`,
    mimes: (field) => `O campo ${field} deve ser um tipo de arquivo vlido.`,
    min: (field, [length]) => `O campo ${field} deve conter pelo menos ${length} caracteres.`,
    not_in: (field) => `O campo ${field} deve ser um valor vlido.`,
    numeric: (field) => `O campo ${field} deve conter apenas nmeros`,
    regex: (field) => `O campo ${field} possui um formato invlido.`,
    required: (field) => `O campo ${field}  obrigatrio.`,
    size: (field, [size]) => `O campo ${field} deve ser menor que ${size} KB.`,
    url: (field) => `O campo ${field} no  uma URL vlida.`,
    currency: (field) => `O campo ${field}  deve ser maior que zero.`
};


